@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

.auth-page {
    margin: 0;
    padding: 100px 20px 20px 20px;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    height: 100vh;
    background: linear-gradient(135deg, #25252b, #1a1a1a);
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
}

.auth-page * {
    box-sizing: border-box;
}

.auth-page .container{
    position: relative;
    width: 750px;
    height: 500px;
    border: 2px solid #ff4d8d;
    box-shadow: 0 0 25px #ff4d8d;
    overflow: hidden;
    border-radius: 20px;
}

.auth-page .container .form-box{
    position: absolute;
    top: 0;
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.auth-page .form-box.Login{
    left: 0;
    padding: 0 40px;
}

.auth-page .form-box.Login .animation{
    transform: translateX(0%);
    transition: .7s;
    opacity: 1;
    transition-delay: calc(.1s * var(--S));
}

.auth-page .container.active .form-box.Login .animation{
    transform: translateX(-120%);
    opacity: 0;
    transition-delay: calc(.1s * var(--D));
}

.auth-page .form-box.Register{
    right: 0;
    padding: 0 60px;
}

.auth-page .form-box.Register .animation{
    transform: translateX(120%);
    transition: .7s ease;
    opacity: 0;
    filter: blur(10px);
    transition-delay: calc(.1s * var(--S));
}

.auth-page .container.active .form-box.Register .animation{
    transform: translateX(0%);
    opacity: 1;
    filter: blur(0px);
    transition-delay: calc(.1s * var(--li));
}

/* Ensure Register form is visible when active */
.auth-page .container.active .form-box.Register {
    z-index: 2;
}

.auth-page .container.active .form-box.Login {
    z-index: 1;
}

.auth-page .form-box h2{
    font-size: 32px;
    text-align: center;
    color: #fff;
    margin-bottom: 30px;
}

.auth-page .form-box .input-box{
    position: relative;
    width: 100%;
    height: 50px;
    margin-top: 25px;
}

.auth-page .input-box input{
    width: 100%;
    height: 100%;
    background: transparent;
    border: none;
    outline: none;
    font-size: 16px;
    color: #fff;
    font-weight: 600;
    border-bottom: 2px solid #fff;
    padding: 0 5px;
    transition: .5s;
}

.auth-page .input-box input:focus,
.auth-page .input-box input:valid{
    border-bottom: 2px solid #ff4d8d;
}

.auth-page .input-box label{
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    font-size: 16px;
    color: #fff;
    transition: .5s;
    pointer-events: none;
}

.auth-page .input-box input:focus ~ label,
.auth-page .input-box input:valid ~ label{
    top: -5px;
    color: #ff4d8d;
}

/* Box icon styles removed for cleaner design */

.auth-page .btn{
    position: relative;
    width: 100%;
    height: 45px;
    background: transparent;
    border-radius: 40px;
    cursor: pointer;
    font-size: 16px;
    font-weight: 600;
    border: 2px solid #ff4d8d;
    overflow: hidden;
    z-index: 1;
    margin-top: 25px;
    color: #fff;
    transition: all 0.3s ease;
}

.auth-page .btn:disabled {
    opacity: 0.6;
    cursor: not-allowed;
}

.auth-page .btn::before{
    content: "";
    position: absolute;
    height: 300%;
    width: 100%;
    background: linear-gradient(#25252b, #ff4d8d, #25252b, #ff4d8d);
    top: -100%;
    left: 0;
    z-index: -1;
    transition: .5s;
}

.auth-page .btn:hover:before{
    top: 0;
}

.auth-page .btn-secondary {
    position: relative;
    width: 100%;
    height: 35px;
    background: transparent;
    border-radius: 40px;
    cursor: pointer;
    font-size: 14px;
    font-weight: 500;
    border: 1px solid #666;
    overflow: hidden;
    z-index: 1;
    margin-top: 15px;
    color: #ccc;
    transition: all 0.3s ease;
}

.auth-page .btn-secondary:hover {
    border-color: #ff4d8d;
    color: #ff4d8d;
}

.auth-page .regi-link{
    font-size: 14px;
    text-align: center;
    margin: 20px 0 10px;
}

.auth-page .regi-link a{
    text-decoration: none;
    color: #ff4d8d;
    font-weight: 600;
    cursor: pointer;
}

.auth-page .regi-link a:hover{
    text-decoration: underline;
}

.auth-page .info-content{
    position: absolute;
    top: 0;
    height: 100%;
    width: 50%;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.auth-page .info-content.Login{
    right: 0;
    text-align: right;
    padding: 0 40px 60px 150px;
}

.auth-page .info-content.Login .animation{
    transform: translateX(0);
    transition: .7s ease;
    transition-delay: calc(.1s * var(--S));
    opacity: 1;
    filter: blur(0px);
}

.auth-page .container.active .info-content.Login .animation{
    transform: translateX(120%);
    opacity: 0;
    filter: blur(10px);
    transition-delay: calc(.1s * var(--D));
}

.auth-page .info-content.Register{
    left: 0;
    text-align: left;
    padding: 0 150px 60px 38px;
    pointer-events: none;
}

.auth-page .info-content.Register .animation{
    transform: translateX(-120%);
    transition: .7s ease;
    opacity: 0;
    filter: blur(10PX);
    transition-delay: calc(.1s * var(--S));
}

.auth-page .container.active .info-content.Register .animation{
    transform: translateX(0%);
    opacity: 1;
    filter: blur(0);
    transition-delay: calc(.1s * var(--li));
}

.auth-page .info-content h2{
    text-transform: uppercase;
    font-size: 36px;
    line-height: 1.3;
    color: #fff;
    margin-bottom: 20px;
}

.auth-page .info-content p{
    font-size: 16px;
    color: #ccc;
    line-height: 1.6;
}

.auth-page .container .curved-shape{
    position: absolute;
    right: 0;
    top: -5px;
    height: 600px;
    width: 850px;
    background: linear-gradient(45deg, #25252b, #ff4d8d);
    transform: rotate(10deg) skewY(40deg);
    transform-origin: bottom right;
    transition: 1.5s ease;
    transition-delay: 1.6s;
}

.auth-page .container.active .curved-shape{
    transform: rotate(0deg) skewY(0deg);
    transition-delay: .5s;
}

.auth-page .container .curved-shape2{
    position: absolute;
    left: 250px;
    top: 100%;
    height: 700px;
    width: 850px;
    background: #25252b;
    border-top: 3px solid #ff4d8d;
    transform: rotate(0deg) skewY(0deg);
    transform-origin: bottom left;
    transition: 1.5s ease;
    transition-delay: .5s;
}

.auth-page .container.active .curved-shape2{
    transform: rotate(-11deg) skewY(-41deg);
    transition-delay: 1.2s;
}

/* Error and Success Messages */
.auth-page .error-message {
    background: rgba(239, 68, 68, 0.1);
    border: 1px solid #ef4444;
    color: #fca5a5;
    padding: 10px;
    border-radius: 8px;
    margin-bottom: 20px;
    text-align: center;
    font-size: 14px;
}

.auth-page .success-message {
    background: rgba(34, 197, 94, 0.1);
    border: 1px solid #22c55e;
    color: #86efac;
    padding: 10px;
    border-radius: 8px;
    margin-bottom: 20px;
    text-align: center;
    font-size: 14px;
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
    .auth-page {
        padding: 100px 20px 20px 20px;
        align-items: flex-start;
        height: 100vh;
        overflow-y: auto;
    }
    
    .auth-page .container {
        width: 100%;
        max-width: 400px;
        height: auto;
        min-height: 500px;
        display: flex;
        flex-direction: column;
        position: relative;
    }
    
    .auth-page .form-box {
        width: 100%;
        height: auto;
        position: static !important;
        padding: 40px 30px;
        left: auto !important;
        right: auto !important;
        top: auto !important;
        display: flex;
        justify-content: center;
        flex-direction: column;
    }
    
    .auth-page .form-box.Login {
        display: flex;
    }
    
    .auth-page .form-box.Register {
        display: none;
    }
    
    .auth-page .container.active .form-box.Login {
        display: none;
    }
    
    .auth-page .container.active .form-box.Register {
        display: flex;
    }
    
    .auth-page .info-content {
        display: none;
    }
    
    .auth-page .curved-shape,
    .auth-page .curved-shape2 {
        display: none;
    }
    
    .auth-page .form-box h2 {
        font-size: 28px;
        margin-bottom: 20px;
        text-align: center;
    }
    
    .auth-page .form-box .input-box {
        margin-top: 20px;
    }
    
    /* Reset animations for mobile */
    .auth-page .form-box .animation {
        transform: none !important;
        opacity: 1 !important;
        filter: none !important;
        transition: none !important;
    }
    
    .auth-page .container.active .form-box.Login .animation {
        transform: none !important;
        opacity: 1 !important;
    }
    
    .auth-page .container.active .form-box.Register .animation {
        transform: none !important;
        opacity: 1 !important;
        filter: none !important;
    }
}

/* Extra small mobile devices */
@media (max-width: 480px) {
    .auth-page {
        padding: 80px 15px 15px 15px;
    }
    
    .auth-page .container {
        max-width: 350px;
        min-height: 450px;
    }
    
    .auth-page .form-box {
        padding: 30px 20px;
    }
    
    .auth-page .form-box h2 {
        font-size: 24px;
        margin-bottom: 15px;
    }
    
    .auth-page .form-box .input-box {
        margin-top: 15px;
        height: 45px;
    }
    
    .auth-page .input-box input {
        font-size: 14px;
    }
    
    .auth-page .btn {
        height: 40px;
        font-size: 14px;
        margin-top: 20px;
    }
    
    .auth-page .btn-secondary {
        height: 32px;
        font-size: 12px;
        margin-top: 12px;
    }
    
    .auth-page .regi-link {
        font-size: 12px;
        margin: 15px 0 5px;
    }
}
